
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500&display=swap');
@import './sass/libs/bootstrap-reboot.min';
@import './sass/blocks/menu';
@import './sass/blocks/modal';
@import './sass/blocks/skills';
@import './sass/blocks/works';
@import './sass/blocks/about';
@import './sass/blocks/contact';
@import './sass/blocks/form';
@import './sass/blocks/main';


body {
  min-height:100vh;
  background: radial-gradient(ellipse at bottom, #1B2735 0%, #090A0F 100%) center center / cover no-repeat;
  margin: 0;
  font-family: 'Roboto Condensed', sans-serif;
  font-family: 'Montserrat', sans-serif;
/*   background-color:#1F5F8B; */
  
}
h1,h2,h3 {
  font-weight: 300;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.container {
  max-width:1140px;
  margin: 0 auto;
}
.title {
  margin-top: 20px;
  font-size: 40px;
  color:#fff;
  text-align: center;
  margin-bottom: 0;
  border-bottom: 4px solid #FCC769;
}

@import './sass/blocks/media';
