@media (max-width:1200px) {
    .container {
        max-width:960px;
    }
    .about {
        &__wrapper {
            grid-template-columns: 30% 70%;
            column-gap:0;
        }
    }
}
@media (max-width:992px) {
    .container {
        max-width:720px;
    }
    .about {
        padding-top: 20px;
        &__wrapper {
            grid-template-columns: 40% 60%;
            column-gap:0;
            align-items: center;
        }
        &__subtitle {
            margin-top: 0;
        }
    }
    .skills {
        padding-top: 20px;
        &-item {
            &__scale {
                width:250px;
            }
        }
    }
    .works {
        padding-top: 20px;
        &__wrapper {
            grid-template-columns: repeat(3,1fr);
        }
    }
    .modal {
        &__screen {
            width:420px;
            
        }
    }
    .form {
        width:70%;
    }
    .contact {
        padding-top: 20px;
    }

}
@media (max-width:768px) {
    .container {
        max-width:540px;
    }
    .menu {
        &__list {
            width:80%;
            margin: 0 auto;
            justify-content: space-around;
        }
        &__link {
            margin-right: 0;
        }
    }
    .about {
        padding-bottom: 20px;
        &__wrapper {
            grid-template-columns: 1fr;
        }
        &__img {
            justify-self:center;
        }
        &__subtitle {
            text-align: center;
            margin-top: 20px;
        }
    }
    .skills {
        padding-bottom: 20px;
        &__wrapper {
            grid-template-columns: 1fr;
        }
    }
    .works {
        &__wrapper {
            grid-template-columns: repeat(2, 240px);
            grid-template-rows: 240px;
            grid-auto-rows: 240px;
        }
        &-item {
            &:nth-child(5) {
                grid-column: unset;
            }
            &:nth-child(6) {
                grid-column: unset;
            }
        }
    }
}
@media (max-width:576px) {
    .container {
        max-width:unset;
        width:100%;
        padding:0px 15px;
    }
    .main {
        padding-top: 165px;
        &__subtitle {
            font-size: 22px;
        }
    }
    .about {
        &__text {
            padding: 0 15px;
        }
    }
    .github-corner {
        display: none;
    }
    .hamburger {
        z-index: 9999;
        display: flex;
        flex-direction: column;
        position:absolute;
        align-items:flex-end;
        top:20px;
        right:20px;
        width:35px;
        height:30px;
        span {
            display: block;
            margin-bottom: 5px;
            width:100%;
            height:4px;
            border-radius: 2px;
            background-color:#fff;
            &:nth-child(2) {
                width:65%;

            }
        }
    }
    .menu {
        position:fixed;
        z-index: 10;
        top:0;
        left:-100%;
        width:50%;
        height:100%;
        background-color:rgba(0,0,0, 0.9);
        transition: all 1s;
        &_active {
            left:0;
        }
        &__close {
            display: block;
            position: absolute;
            top:-12px;
            right:10px;
            font-size: 50px;
            font-weight: 400;
            color:#fff;
        }
        &__social {
            z-index: 20;
            position:absolute;
            bottom:30px;
            width:65%;
            left:50%;
            transform: translateX(-50%);
            display:flex;
            justify-content: space-around;
            a {
                background-color:#fff;
                border-radius: 50%;
                img {
                    width:35px;
                }
            }
        }
        &__list {
            margin-top: 100px;
            flex-direction: column;
            align-items:flex-start;
            padding-left: 40px;
        }
        &__item {
            margin-bottom: 15px;
        }
        &__link {
            font-size: 22px;
            text-transform:capitalize;
            color:#FCC769;
            &:hover {
                color:#FCC769;
                &:after {
                    display: none;
                }
            }
            &_active {
                color:#FCC769;
                font-weight: 700;
                &:after {
                    top:30px;
                }
                
            }
        }
    }
    .skills {
        &-item {
            &__scale {
                width:60%;
            }
        }
    }
    .works {
        &__wrapper {
            grid-template-columns: 240px;
        }
        &-item {
            &:hover {
                transform: unset;
            }
        }
    }
    .modal {
        &__content {
            width:80%;
        }
        &__screen {
            display: none;
        }
        &__footer {
            flex-direction: column;
            justify-content: center;
            align-items:center;
        }
        &__descr {
            text-align: center;
            margin-top: 20px;
        }
        &__links {
            margin-top: 20px;
        }
    }
    .form {
        width: 90%;
    }
    .title {
        margin-top: 40px;
        font-size: 30px;
    }
}
@media (max-width:460px) {
    .menu {
        width:70%;
        &__list {
            padding-left: 20px;
        }
    }
    .form {
        width:100%;
        padding: 0px 10px;
        &__button {
            width:100%;
        }
    }
}