.modal {
    width: 100%;
    height:100%;
    background-color: rgba(0,0,0, 0.7);
    position:fixed;
    top:0;
    left:0;
    opacity:0;
    pointer-events: none;
    transition: all 0.5s;
    display: flex;
    justify-content: center;
    align-items: center;
    &_active {
        opacity:1;
        pointer-events: all;
        transition: all 0.5s;
    }
    &__content {
        background-color: #fff;
        padding: 20px;
        border-radius: 10px;
        transform: scale(0.5);
        transition: all 0.5s;
        &_active {
            transform:scale(1);
            transition: all 0.5s;
        }
    }
    &__close {
        font-size: 40px;
        position:absolute;
        right:10px;
        top: -10px;
        cursor: pointer;
    }
}
