.works {
    padding-top: 50px;
    padding-bottom: 40px;
    &__wrapper {
        margin-top: 40px;
        display: grid;
        grid-template-columns: repeat(4,200px);
        grid-template-rows: repeat(2, 200px);
        grid-auto-rows: auto;
        column-gap:30px;
        row-gap:30px;
        justify-content: center;
    }
    &__icons {
        display: flex;
        img {
            width:40px
        }
    }
    &-item {
        background-color:#fff;
        display: flex;
        flex-direction: column;
        transform:scale(1);
        transition: all 0.5s;
        cursor:pointer;
        border-radius: 5px;
        &:nth-child(5) {
            grid-column: 2/3;
        }
        &:nth-child(6) {
            grid-column: 3/4;
        }
        &:hover {
            transform:scale(1.1);
        }
        img {
            width:100%;
            height:100%;
        }
        &__button {
            margin-top: 20px;
            width:60px;

        }
    }
}
.modal {
    &__title {
        font-size: 30px;
        text-align: center;
        font-weight: 500;
        margin-bottom: 0;
        border-bottom: 4px solid #FCC769;
    }
    &__screen {
        display: block;
        margin-top: 20px;
        width:600px;
        box-shadow: 1px 1px 10px #000;
    }
    &__links {
        display:flex;
        justify-content: space-around;
        align-items: flex-end;
        width:200px;
    }
    &__link {
        display: block;
        padding: 10px;
        border-radius: 5px;
        color:#fff;
        background-color:#0000FF;
        &:hover {
            text-decoration: none;
            color:#fff;
            background-color:#0000C3;
        }
    }
    &__descr{
        margin-top: 10px;
    }
    &__footer {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
    }
}