.skills {
    padding-top: 50px;
    &__wrapper {
        margin-top: 40px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows:auto;
        grid-auto-rows: auto;
        column-gap:20px;
        row-gap:20px;
    }
    &-item {
        display: flex;
        justify-content: center;
        align-items:center;
        &__scale {
            width:300px;
            height:15px;
            border:2px solid #fff;
            border-radius: 15px;
            
            div {
                border-radius: 15px;
                height: 100%;
                background-color:green;
            }
        }
        &__img {
            width:80px;
        }
    }
}