.menu {
    padding-top: 40px;
    &__close {
        display: none;
    }
    &__social {
        display: none;
    }
    &__list {
        padding-left: 0;
        list-style-type: none;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        
    }
    &__link {
        color: #A9A9A9;
        padding:2px 0px;
        font-size: 16px;
        font-weight: 500;
        text-transform: uppercase;
        margin-right: 40px;
        transition: all 1s;
        position: relative;
        &:after {
            transition: all .5s;
            content: '';
            position:absolute;
            top:22px;
            left:50%;
            transform: translateX(-50%);
            height:2px;
            width:0;
            background-color:#fff;
        }
        &:hover {
            &:after {
                width:100%;
            }
            color:#fff;
            text-decoration: none;
        }
        &_active {
           &:after {
               width:100%;
           }
          
           color:#fff;
        }
    }
}
.hamburger {
    display: none;
}