
.main {
    padding-top: 100px;
    padding-bottom: 20px;
    &__title {
        color:#fff;
        font-size: 50px;
        font-weight: 300;
        text-align: center;
    }
    &__subtitle {
        text-align: center;
        font-weight: 300;
        font-size: 40px;
        color:#fff;
    }
    &__avatar {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__link {
        padding:10px;
        border:1px solid #fff;
        border-radius: 5px;
        color:#fff;
        font-size: 20px;
        display: block;
        margin:20px auto 0 auto;
        width:180px;
        text-align: center;
        &:hover {
            color:#fff;
            text-decoration: none;
            background-color: grey;
        }

    }
}
