.about {
    padding-top: 50px;
    &__subtitle {
        font-size: 30px;
        margin-top: 20px;
    }
    &__wrapper {
        margin-top: 40px;
        display: grid;
        grid-template-columns: 25% 75%;
        grid-template-rows: auto;
        grid-auto-rows: auto;
        column-gap:20px;
    }
    &__img {
        width:250px;
        justify-self: flex-start;
       img {
            width:100%;
            border-radius:10px;
       }
    }
    &__description {
        color: #fff;
    }
    &__text {
        margin-top: 20px;
        font-size: 16px;
        line-height: 32px;
        font-weight: 300;
    }
}