.form {
    width:50%;
    margin: 50px auto 0 auto;
    &__list {
        display: flex;
        flex-direction: column;
        padding-left: 0;
        list-style-type: none;
    }
    &__item {
        position: relative;
    }
    &__input, &__textarea, &__button {
        background-color:transparent;
        border: 1px solid #FCC769;
        border-radius: 5px;
        color:#fff;
        margin-bottom: 30px;
        padding:5px 10px;
        width:100%;
        &:focus {
            outline:none;
        }
    }
    &__textarea {
        height: 150px;
    }
    &__input:-webkit-autofill,&__input:-webkit-autofill:hover, &__input:-webkit-autofill:focus,
    &__textarea:-webkit-autofill, &__textarea:-webkit-autofill:hover,&__textarea:-webkit-autofill:focus {
        transition: all 5000s ease-in-out 0s;
    }
    &__button {
        width:50%;
        display: block;
        margin: 0 auto;
        &:hover {
            background-color:rgba(252,199,105, 0.2);
        }
    }
    &__error {
        top:-20px;
        left:10px;
        position: absolute;
        color:red;
        font-size: 12px;
    }
    &-thanks {
        color:#fff;
        margin-top:50px;
        text-align: center;
        &__subtitle {
            font-weight: 300;
            margin-top: 20px;
        }
    }
    
}